<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
body {
  background-color: #fafafc;
}
</style>
<template>
    <div class='ruleSetting'>
        <div class='ruleSetting-box'>
            <div class='title'>
                <span class='btn'>
                    <el-button type="primary"></el-button>
                    <el-button type="primary" @click='cancel'></el-button>
                </span>
            </div>
            <div class='infoform'>
                <div class='text'></div>
                <el-form v-model="xx">
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.deposit"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.no_price"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.allow"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.deposit"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.no_price"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input'>
                        <el-input v-model="xx.allow"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div class='info'>
                <div class='title'>
                    <div class='text'></div>
                    <span class='btn'>
                        <el-button type="primary"></el-button>
                    </span>
                </div>
                <el-table :data="refund.file" border>
                    <el-table-column label="" align='center' width="70px">
                        <template slot-scope="scope">{{getIndex(scope)}}</template>
                    </el-table-column>
                    <el-table-column property="project_code" label="" align='center'></el-table-column>
                    <el-table-column property="project_name" label="" align='center'></el-table-column>
                    <el-table-column property="absolute_address" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_name" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
                    <el-table-column property="operation" label="/" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>

</template>
<script>
export default {
  data() {
    return {
      xx: [],
      refund: []
    };
  },
  mounted() {},
  methods: {
    cancel() {
      this.$router.push({
        name: "ruleOfMaid"
      });
    }
  }
};
</script>



