<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
.quitPeople {
  .el-table thead {
    color: #333;
  }
  .el-table th {
    padding: 8px 5px;
  }
  .el-table td {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0 20px 20px 20px;
    margin: 0;
  }
}
</style>

<template>
  <div class='quitPeople'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="search-block">
          <el-input class='query' v-model="searchObj.search" placeholder="/"></el-input>
          <el-button @click="getQuitList" icon="el-icon-search" circle></el-button>
        </div>
      </div>

    </div>
    <template>
      <el-table class="mt-20" :data="tableData" border style="width: 100%">
        <el-table-column label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column prop="account" label="" align='center'></el-table-column>
        <el-table-column prop="name" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="tel" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="role" label="" align='center'>
          <template slot-scope="scope">{{getRole(scope.row.role)}}</template>
        </el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="department" label="" align='center'></el-table-column>
        <!-- <el-table-column prop="position" label="" align='center'></el-table-column> -->
        <el-table-column prop="city" label="" align='center'></el-table-column>
        <el-table-column prop="district" label="" align='center'></el-table-column>
        <el-table-column prop="entry_time" label="" align='center'></el-table-column>
        <el-table-column prop="create_time" label="" align='center'></el-table-column>
        <!-- <el-table-column prop="remark" label="" align='center' width="190px"></el-table-column> -->
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='showSee(scope.row)'></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
      <el-dialog title="" :visible.sync="dialogFormVisible" class='dialog' @close="cancel">
        <div class='btn'>
          <el-button type='primary' @click='cancel'></el-button>
        </div>
        <div class='num_set'></div>
        <el-form v-model="examinePeople">
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{role(examinePeople.role)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.account}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.department}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.position}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.entry_time}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <span>()</span>
              <el-button type='text' @click='seeimgUrl'></el-button>
            </div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{sex(examinePeople.sex)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.tel}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.birth}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.id_card}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.bank_name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.bank_card}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border3'>{{examinePeople.city_name+examinePeople.district_name+examinePeople.absolute_address}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <span>()</span>
              <el-button type='text' @click='seeIdCard'></el-button>
            </div>
          </el-form-item>
          <el-form-item class='input2'>
            <div></div>
            <div class='border height'>{{examinePeople.slef_desc}}</div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.quit ? examinePeople.quit.create_time : ''}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'> {{quitType(examinePeople.quit? examinePeople.quit.type : '')}}</div>
          </el-form-item>
          <el-form-item class='input2'>
            <div></div>
            <div class='border height'>{{examinePeople.quit ? examinePeople.quit.remark : ''}}</div>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="" :visible.sync="showImgUrl">
        <img class='heightWidth' v-if='examinePeople.img_url' :src="this.url + examinePeople.img_url" />
        <img class='heightWidth' v-else src="../../../assets/images/head.png" />
      </el-dialog>
      <el-dialog title="" :visible.sync="showIdCard" class='showIdCard'>
        <img class='heightWidth' v-if='examinePeople.card_front' :src="this.url + examinePeople.card_front" />
        <img class='heightWidth' v-else src="../../../assets/images/idCard.png" />
        <img class='heightWidth' v-if='examinePeople.card_back' :src="this.url + examinePeople.card_back" />
        <img class='heightWidth' v-else src="../../../assets/images/Document_2@2x.png" />
      </el-dialog>
    </template>
  </div>
</template>
<script>
export default {
  data() {
    return {
      url: "http://120.27.21.136:2798/",
      tableData: [],
      dialogFormVisible: false,
      searchObj: {
        search: "",
        page: 1
      },
      pageSize: 0,
      total: 0,
      examinePeople: {
        name: "",
        role: "",
        account: "",
        department: "",
        position: "",
        img_url: "",
        entry_time: "",
        project_name: "",
        sex: "",
        tel: "",
        birth: "",
        absolute_address: "",
        slef_desc: "",
        bank_card: "",
        bank_name: "",
        card_url: "",
        id_card: "",
        quit: {
          his_id: "",
          auth_id: "",
          create_time: "",
          type: "",
          remark: ""
        }
      },
      showImgUrl: false,
      showIdCard: false
    };
  },
  mounted() {
    this.getQuitList();
  },
  methods: {
    seeimgUrl() {
      this.showImgUrl = true;
    },
    seeIdCard() {
      this.showIdCard = true;
    },
    getRole(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    async showSee(row) {
      this.dialogFormVisible = true;
      Object.assign(this.examinePeople, row);
      this.examinePeople.agent_id = row.agent_id;
      let res = await this.api.getQuitInfo(this.examinePeople);
      if (res.code == 200) {
        this.examinePeople = res.data;
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    role(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    quitType(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    sex(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    cancel() {
      this.dialogFormVisible = false;
    },
    search() {
      this.searchObj.page = 1;
      this.getQuitList();
    },
    getIndex(row) {
      let index = row.$index + 1 + (this.searchObj.page - 1) * this.pageSize;
      return index;
    },
    pageChange(page) {
      this.searchObj.page = page;
      this.getQuitList();
    },
    async getQuitList() {
      let res = await this.api.getQuitList(this.searchObj);
      if ((res.code = 200)) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.pageSize = res.data.per_page;
      }
    }
  }
};
</script>





