<style lang="less" scoped >
.projectInfo {
  width: 900px;
  margin: 0 auto;
  border: 1px solid #999;
  text-align: left;
  padding: 15px 30px;
  margin-top: 20px;
  .input {
    width: 190px;
    display: inline-block;
    padding-right: 31px;
  }
  .input1 {
    padding-top: 10px;
    width: 300px;
  }
  .el-form-item {
    margin: 0;
  }
  .text {
    vertical-align: middle;
    font-size: 14px;
    color: #333;
    line-height: 40px;
    padding: 0 12px 0 0;
  }
  .title-text {
    font-size: 16px;
    padding: 5px 0;
    position: relative;
    color: #333;
  }
  .dialog-footer {
    position: absolute;
    right: 0;
    top: 0;
  }
}
</style>
<style lang="less">
.projectInfo {
  .el-form-item__label {
    float: none;
    color: #333;
  }
  .el-table td,
  .el-table th {
    padding: 4px 0;
    color: #333;
  }
}
</style>

<template>

  <div class='projectInfo'>
    <div class='title-text'>
      <div slot="footer" class="dialog-footer">
        <el-button @click='cancel'> </el-button>
        <el-button type="primary" v-if="!isDisable()" @click='submit'> </el-button>
      </div>
    </div>
    <el-form :model="submitForm" class='form' :disabled="!operationType==1">
      <div>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.project_code" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.project_name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </div>
      <!--  -->
      <div class="text"></div>
      <el-form-item prop="property_tags">
        <el-checkbox v-model="submitForm.property_tags" v-for="item in typeOptions" :key="item.param_id" :label="item.param_id">{{item.param}}</el-checkbox>
      </el-form-item>
      <div class='text'></div>
      <el-form-item>
        <city-selector :province.sync="submitForm.province" :city.sync="submitForm.city" :district.sync="submitForm.district" />
        <el-input class='input1' v-model="submitForm.absolute_address"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.project_hold_name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.project_hold_phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.developer_name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.statement_company" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.subordinate_company" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.company_relation" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <!-- <div v-if="this.auditing_state==1">
      <div class='title-text'></div>
      <el-form :model="submitForm.auditProject">
        <div>
          <el-form-item label="" class='input'>
            <el-input v-model="submitForm.fp_name" auto-complete="off" :disabled="operationType==0"></el-input>
          </el-form-item>
          <el-form-item label="" class='input'>
            <el-input v-model="submitForm.fp_time" auto-complete="off" :disabled="operationType==0"></el-input>
          </el-form-item>
          <el-form-item label="" class='input'>
            <el-input v-model="submitForm.remark" auto-complete="off" :disabled="operationType==0"></el-input>
          </el-form-item>
        </div>
      </el-form>
    </div> -->
    <div v-if="this.auditing_state==1||operationType==0">
      <div class='title-text'></div>
      <el-table :data="peopleInfo" border>
        <el-table-column label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column property="account" label="" align='center'></el-table-column>
        <el-table-column property="name" label="" align='center'></el-table-column>
        <el-table-column property="tel" label="" align='center'></el-table-column>
        <el-table-column property="department" label="" align='center'></el-table-column>
        <el-table-column property="position" label="" align='center'></el-table-column>
        <el-table-column property="create_time" label="" align='center'></el-table-column>
        <el-table-column property="entry_time" label="" align='center'></el-table-column>
      </el-table>
    </div>
    <div v-if='operationType==1||operationType==0'>
      <div class='title-text'></div>
      <el-table :data="gridData" border>
        <el-table-column prop="state" label="" align='center'>
          <template slot-scope="scope">{{state(scope.row.state)}}</template>
        </el-table-column>
        <el-table-column prop="end_state" label="" align='center'>
          <template slot-scope="scope">{{end_state(scope.row.end_state)}}</template>
        </el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type='text'></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import CitySelector from "../../../components/CitySelector";
export default {
  data() {
    return {
      submitForm: {
        province: "",
        city: "",
        district: "",
        project_id: "",
        rule_id: "",
        auditProject: {},
        property_tags: [],
        state: ""
      },
      peopleInfo: [],
      project_id: "",
      rule_id: "",
      typeOptions: [],
      operationType: 0,
      auditing_state: "",
      gridData: []
    };
  },
  mounted() {
    this.auditing_state = this.$route.params.auditing_state;
    this.project_id = this.$route.params.project_id;
    this.rule_id = this.$route.params.rule_id;
    if (this.$route.params.project_id) {
      this.operationType = this.$route.params.operationType;
      this.getProjectDetail();
      this.getType();
    } else {
      this.$router.push({ name: "distribution" });
    }
  },
  methods: {
    getIndex(row) {
      let index = row.$index + 1;
      return index;
    },
    end_state(row) {
      if (row == 0) {
        return "";
      } else {
        return "";
      }
    },
    state(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    async getType() {
      let res = await this.api.getTags();
      if (res.code == 200) {
        this.typeOptions = res.data;
      }
    },
    isDisable() {
      if (this.operationType == 0) {
        return true;
      } else if (this.operationType == 1) {
        return false;
      } else {
        return false;
      }
    },
    async getProjectDetail() {
      Object.assign(this.submitForm, this.$route.params.projectInfo);
      if (this.operationType == 0) {
        let res = await this.api.getRole({
          project_id: this.project_id,
          rule_id: this.rule_id
        });
        if (res.code == 200) {
          Object.assign(this.submitForm, res.data.project);
          let property_tags = res.data.project.property_tags;
          let arr = [];
          for (let type of property_tags) {
            arr.push(type.property_tag_id);
          }
          this.submitForm.property_tags = arr;
          this.gridData = res.data.broker;
          this.peopleInfo = res.data.agent;
        }
      } else {
        Object.assign(this.submitForm, this.$route.params.projectInfo);
        let res = await this.api.applyProject({ project_id: this.project_id });
        if (res.code == 200) {
          Object.assign(this.submitForm, res.data);
          let property_tags = res.data.property_tags;
          let arr = [];
          for (let type of property_tags) {
            arr.push(type.property_tag_id);
          }
          this.submitForm.property_tags = arr;
        }
      }
    },
    async search() {
      let res = await this.api.changeProjectList({
        project_id: this.submitForm.project_id
      });
      if (res.code == 200) {
        this.sumbitForm = res.data;
      }
    },
    submit() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          let res = await this.api.changeProjectAdd({
            project_id: this.project_id
          });
          if (res.code == 200) {
            this.$message({
              type: "success",
              message: "!"
            });
            let rule_id = res.data;
            this.$router.push({
              name: "ruleOfMaid",
              params: { project_id: this.project_id, rule_id: rule_id }
            });
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    cancel() {
      if (this.$route.params.operationType != undefined) {
        this.$router.push({ name: "distribution" });
      } else {
        this.$router.push({ name: "startApply" });
      }
    }
  },
  components: {
    CitySelector: CitySelector
  }
};
</script>



