<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
.examinePeople {
  .el-table thead {
    color: #333;
  }
  .el-table th {
    padding: 8px 5px;
  }
  .el-table td {
    padding: 0;
  }
  .dialog {
    text-align: left;
    position: fixed;
    top: -10%;
    left: 0;
    .el-dialog__title {
      font-size: 26px;
    }
    .el-dialog__body {
      padding: 0px 20px 20px 20px;
    }
    .el-form-item {
      margin: 0;
    }
    .btn {
      position: absolute;
      right: 0;
      top: 15px;
      margin-right: 20px;
    }
    .num_set {
      font-size: 18px;
      padding: 5px 0;
    }
    .border {
      width: 140px;
      height: 35px;
      border: 1px solid#bbb;
      border-radius: 6px;
      padding-left: 10px;
    }
    .border.img {
      border: none;
    }
    .el-form-item__content {
      text-align: left;
    }
    .border.img {
      width: 240px;
      border: none;
      .text {
        padding-right: 20px;
        font-size: 14px;
      }
    }
    .border.height {
      height: 70px;
      width: 400px;
    }
    .border.width {
      width: 340px;
    }
    .input1 {
      display: inline-block;
      padding-right: 30px;
      text-align: left;
      vertical-align: top;
    }
    .img-text {
      display: inline-block;
      vertical-align: top;
    }
    .showImg {
      width: 800px;
      position: fixed;
      top: 20%;
      left: 30%;
    }
  }
}
</style>

<template>
  <div class='examinePeople'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="left">
          <el-button class='tip' :class="tipActiveIndex==1?'active':''" type="text" @click="clickTip(1)"></el-button>
          <el-button class='tip' :class="tipActiveIndex==2?'active':''" type="text" @click="clickTip(2)"></el-button>
          <el-button class='tip' :class="tipActiveIndex==0?'active':''" type="text" @click="clickTip(0)"></el-button>
          <div class="search-block">
            <el-input class='query' v-model="searchObj.search" placeholder="/"></el-input>
            <el-button icon="el-icon-search" circle @click='getExList'></el-button>
          </div>
        </div>
      </div>
    </div>
    <template>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column prop="account" label="" align='center'></el-table-column>
        <el-table-column prop="name" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="tel" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="role" label="" align='center'>
          <template slot-scope="scope">{{getRole(scope.row.role)}}</template>
        </el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="department" label="" align='center'></el-table-column>
        <!-- <el-table-column prop="position" label="" align='center'></el-table-column> -->
        <el-table-column prop="city_name" label="" align='center'></el-table-column>
        <el-table-column prop="district_name" label="" align='center'></el-table-column>
        <el-table-column prop="state" label="" align='center'>
          <template slot-scope="scope">{{auditingState(scope.row.state)}}</template>
        </el-table-column>
        <!-- <el-table-column prop="remark" label="" align='center' width="160px"></el-table-column> -->
        <el-table-column prop="entry_time" label="" align='center'></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='examine(scope.row)' v-if='scope.row.state==2'></el-button>
            <el-button type="text" @click='showSee(scope.row)' v-if='scope.row.state==0'></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
      <el-dialog title="" :visible.sync="dialogFormVisible" class='dialog' @close="cancel">
        <div class='btn'>
          <el-button type='primary' @click="check(1)" v-if="operationType==0"></el-button>
          <el-button type='primary' @click="showCheck(0)" v-if="operationType==0"></el-button>
          <el-button type='primary' @click='cancel'></el-button>
        </div>
        <div class='num_set'></div>
        <el-form v-model="examinePeople">
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{role(examinePeople.role)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.account}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.department}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.position}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.entry_time}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.project_name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <span>()</span>
              <el-button type='text' @click='seeimgUrl'></el-button>
            </div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{sex(examinePeople.sex)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.tel}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.birth}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border width'>{{examinePeople.city_name+examinePeople.district_name+examinePeople.absolute_address}}</div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.auditing_name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.auditing_time}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border height'>{{examinePeople.remark}}</div>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="" :visible.sync="refuseInfo" class='dialog' @close="cancelRefuseInfo">
        <el-form>
          <el-form-item label="">
            <el-input v-model="remark" class='textarea' type="textarea" />
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelRefuseInfo"> </el-button>
          <el-button type="primary" @click="check(0)"> </el-button>
        </div>
      </el-dialog>
      <el-dialog title="" :visible.sync="showImgUrl">
        <img class='heightWidth' v-if='examinePeople.img_url' :src="this.url + examinePeople.img_url" />
        <img class='heightWidth' v-else src="../../../assets/images/head.png" />
      </el-dialog>
    </template>
  </div>
</template>
<script>
export default {
  data() {
    return {
      url: "http://120.27.21.136:2798/",
      tableData: [],
      searchObj: {
        tag_search: "",
        search: "",
        page: 1
      },
      pageSize: 0,
      total: 0,
      tipActiveIndex: 1,
      dialogFormVisible: false,
      refuseInfo: false,
      examinePeople: {
        name: "",
        role: "",
        account: "",
        department: "",
        position: "",
        img_url: "",
        entry_time: "",
        project_name: "",
        sex: "",
        tel: "",
        birth: "",
        absolute_address: "",
        city_name: "",
        district_name: "",
        agent_id: null,
        project_id: null,
        id: null,
        remark: ""
      },
      showImgUrl: false,
      submitForm: {},
      remark: "",
      operationType: 0 //0  1 
    };
  },

  mounted() {
    this.getExList();
  },
  methods: {
    showCheck(type) {
      this.refuseInfo = true;
    },
    seeimgUrl() {
      this.showImgUrl = true;
    },
    async check(type) {
      this.submitForm.type = type;
      let temp = Object.assign({}, this.submitForm);
      if (type == 0) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(async () => {
            temp.remark = this.remark;
            let res = await this.api.exPeople(temp);
            if (res.code == 200) {
              this.$message({
                type: "success",
                message: "!"
              });
            }
            this.getExList();
            this.cancelRefuseInfo();
            this.cancel();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(async () => {
            let res = await this.api.exPeople(temp);
            if (res.code == 200) {
              this.$message({
                type: "success",
                message: "!"
              });
            }
            this.getExList();
            this.cancelRefuseInfo();
            this.cancel();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    async examine(row) {
      this.operationType = 0;
      this.dialogFormVisible = true;
      this.examinePeople = row;
      this.submitForm.project_id = row.project_id;
      this.submitForm.agent_id = row.agent_id;
      this.submitForm.id = row.id;
      let res = await this.api.getExInfo(this.examinePeople);
      if (res.code == 200) {
        Object.assign(this.examinePeople, res.data);
      }
    },
    async showSee(row) {
      this.operationType = 1;
      this.dialogFormVisible = true;
      Object.assign(this.examinePeople, row);
      let res = await this.api.getRefuseInfo(this.examinePeople);
      if (res.code == 200) {
        this.examinePeople = res.data;
      }
    },
    getRole(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    role(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    sex(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    cancel() {
      this.dialogFormVisible = false;
    },
    cancelRefuseInfo() {
      this.refuseInfo = false;
      this.remark = "";
    },
    async getExList() {
      let res = await this.api.getExList(this.searchObj);
      if ((res.code = 200)) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.pageSize = res.data.per_page;
      }
    },
    pageChange(page) {
      this.searchObj.page = page;
      this.getExList();
    },
    getIndex(row) {
      let index = row.$index + 1 + (this.searchObj.page - 1) * this.pageSize;
      return index;
    },
    clickTip(index) {
      this.tipActiveIndex = index;
      this.searchObj.tag_search = index;
      this.search();
    },
    search() {
      this.searchObj.page = 1;
      this.getExList();
    }
  }
};
</script>





