<style lang="less" scoped src='./index.less'></style>
<style lang="less">
body {
  background-color: #fafafc;
}
.ruleOfMaid {
  .el-table th {
    padding: 5px 0px;
  }
  .el-table td {
    padding: 0;
  }
  .el-form-item {
    padding-bottom: 15px;
  }
}
</style>

<template>
    <div class='ruleOfMaid'>
        <div class='ruleOfMaid-box'>
            <div class='title'>
                <span class='btn'>
                    <el-button type="primary" @click='sumbit'></el-button>
                    <el-button type="primary" @click='cancel'></el-button>
                </span>
            </div>
            <div class='infoform'>
                <div class='text'></div>
                <el-form :model="sumbitForm" :rules="rules" ref="sumbitForm">
                    <!-- <el-form-item label="" class='input'>
                        <el-input v-model="sumbitForm.deposit"></el-input>
                    </el-form-item> -->
                    <el-form-item label="" class='input' prop="project_docker">
                        <el-input v-model="sumbitForm.project_docker"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input' prop="project_docker_tel">
                        <el-input v-model="sumbitForm.project_docker_tel"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="" class='input'>
                        <el-input v-model="sumbitForm.sell_docker"></el-input>
                    </el-form-item> -->
                    <el-form-item label="" class='input' prop="sell_docker">
                        <el-input v-model="sumbitForm.sell_docker"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input' prop="sell_docker_tel">
                        <el-input v-model="sumbitForm.sell_docker_tel"></el-input>
                    </el-form-item>
                    <el-form-item label="" class='input' prop="compact_begin">
                        <el-date-picker v-model="sumbitForm.compact_begin" type="date" placeholder="" class="block"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" class='input' prop="compact_end">
                        <el-date-picker v-model="sumbitForm.compact_end" type="date" placeholder="" class="block"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" class='input' prop="act_start">
                        <el-date-picker v-model="sumbitForm.act_start" type="date" placeholder="" class="block"></el-date-picker>
                    </el-form-item>
                    <!-- <el-form-item label="" class='input'>
                        <el-input v-model="sumbitForm.allow"></el-input>
                    </el-form-item> -->
                </el-form>
            </div>
            <div class='info'>
                <div class='title'>
                    <div class='text'></div>
                    <span class='btn'>
                        <el-upload :auto-upload="false" action="" :on-change="fileUpload">
                            <el-button ref="uploadBtn" type="primary" :show-file-list="false"></el-button>
                        </el-upload>
                    </span>
                </div>
                <el-table :data="refund" border>
                    <el-table-column label="" prop="file_name" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope='scope'>
                            <a target="_blank" :href="'http://120.27.21.136:2798/' + scope.row.url"></a>
                        </template>
                    </el-table-column>
                    <el-table-column property="uploader" label="" align='center'></el-table-column>
                    <el-table-column property="create_time" label="" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope="scope">
                            <el-button type="primary"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class='info'>
                <div class='title'>
                    <div class='text'></div>
                    <span class='btn'>
                        <el-button type="primary" @click='addShow'></el-button>
                    </span>
                </div>
                <el-table :data="ruleForm" border>
                    <el-table-column label="" align='center' width="70px">
                        <template slot-scope="scope">{{getIndex(scope)}}</template>
                    </el-table-column>
                    <el-table-column property="project_code" label="" align='center'></el-table-column>
                    <el-table-column property="project_name" label="" align='center'></el-table-column>
                    <el-table-column property="absolute_address" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_name" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_phone" label="/" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class='info'>
                <div class='title'>
                    <div class='text'></div>
                    <span class='btn'>
                        <el-button type="primary"></el-button>
                    </span>
                </div>
                <el-table :data="ruleForm" border>
                    <el-table-column label="" align='center' width="70px">
                        <template slot-scope="scope">{{getIndex(scope)}}</template>
                    </el-table-column>
                    <el-table-column property="project_code" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_phone" label="/" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class='info'>
                <div class='title'>
                    <div class='text'></div>
                    <span class='btn'>
                        <el-button type="primary"></el-button>
                    </span>
                </div>
                <el-table :data="ruleForm" border>
                    <el-table-column label="" align='center' width="70px">
                        <template slot-scope="scope">{{getIndex(scope)}}</template>
                    </el-table-column>
                    <el-table-column property="project_code" label="" align='center'></el-table-column>
                    <el-table-column property="project_hold_phone" label="/" align='center'></el-table-column>
                    <el-table-column label="" align='center'>
                        <template slot-scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      rules: {
        project_docker: [
          {
            pattern: /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/,
            required: true,
            message: ",2-5",
            change: "change"
          }
        ],
        project_docker_tel: [
          {
            required: true,
            message: "11",
            change: "change",
            pattern: /^1[34578]\d{9}$/
          }
        ],
        sell_docker: [
          {
            pattern: /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/,
            required: true,
            message: ",2-5",
            change: "change"
          }
        ],
        sell_docker_tel: [
          {
            required: true,
            message: "11",
            change: "change",
            pattern: /^1[34578]\d{9}$/
          }
        ],
        compact_begin: [
          {
            required: true,
            message: ""
          }
        ],
        compact_end: [
          {
            required: true,
            message: ""
          }
        ],
        act_start: [
          {
            required: true,
            message: ""
          }
        ]
      },
      sumbitForm: {
        project_docker: "",
        project_docker_tel: "",
        sell_docker: "",
        sell_docker_tel: "",
        compact_begin: "",
        compact_end: "",
        act_start: ""
      },
      refund: [],
      ruleForm: [],
      project_id: "",
      rule_id: ""
    };
  },
  mounted() {
    this.project_id = this.$route.params.project_id;
    this.rule_id = this.$route.params.rule_id;
  },
  methods: {
    async fileUpload(fileObj) {
      this.fileObject = fileObj;
      let flag = false;
      if (
        this.fileObject.name.indexOf(".docx") > -1 ||
        this.fileObject.name.indexOf(".doc") > -1
      ) {
        flag = true;
      }
      if (!flag) {
        this.$message({
          type: "error",
          message: "docxdoc"
        });
        return;
      }
      let file = this.fileObject.raw;
      let formData = new FormData();
      formData.append("url", file);
      let res = await this.api.uploadBrokerAgreement(formData);
      if (res.code == 200) {
        let projectAgreementTemp = {};
        projectAgreementTemp.uploader = res.data.uploader;
        projectAgreementTemp.url = res.data.img_url;
        projectAgreementTemp.file_name = fileObj.name;
        this.form.project_agreement = [];
        this.form.project_agreement.push(projectAgreementTemp);
      }
    },

    sumbit() {
      this.$refs["sumbitForm"].validate(async valid => {
        if (valid) {
          let temp = Object.assign({}, this.sumbitForm);
          temp.project_id = this.project_id;
          temp.rule_id = this.rule_id;
          let res = await this.api.addRule(temp);
          if (res.code == 200) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.$router.push({
              name: "distribution"
            });
          }
        } else {
          return false;
        }
      });
    },

    addShow() {
      this.$router.push({
        name: "ruleSetting"
      });
    },
    cancel() {
      this.$router.push({
        name: "startApply"
      });
    }
  }
};
</script>



