<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
.personPrice {
  .el-table--border,
  .el-table--group {
    margin-top: 10px;
  }
  .el-table th {
    padding: 8px 0px;
     color: #333;
  }
  .el-table td {
    padding: 5px 0;
     color: #333;
  }
  .el-dialog__body {
    padding-top: 0;
  }
}
</style>
<template>
  <div class='personPrice'>
    <div class='table' v-if='personType==0'>
      <div class='title'>
        <span class='title-text'></span>
        <span class='search-block'>
          <span>
            <el-input v-model="searchObj.search" class='query' placeholder="/"></el-input>
            <el-button @click="getPersonInfoList" icon="el-icon-search" circle class='mt-10'></el-button>
          </span>
          <el-button class='pos-btn' type="primary" @click='cancel'></el-button>
        </span>
      </div>
      <div class='text'>
        <span class='price'>:{{sumbitForm.total_price}}</span>
        <span class='price'>:{{sumbitForm.count}}</span>
      </div>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column prop="client_id" label="" align='center'></el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="house_info" label="" align='center'></el-table-column>
        <el-table-column prop="name" label="" align='center'></el-table-column>
        <el-table-column prop="tel" label="" align='center'></el-table-column>
        <el-table-column prop="broker_type" label="" align='center'>
          <template slot-scope="scope">{{brokerType(scope.row.broker_type)}}</template>
        </el-table-column>
        <el-table-column prop="create_time" label="" align='center' width="160px"></el-table-column>
        <el-table-column prop="broker_num" label="" align='center'></el-table-column>
        <el-table-column prop="commission_way" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="pay_state" label="" align='center'>
          <template slot-scope="scope">{{state(scope.row.pay_state)}}</template>
        </el-table-column>
        <el-table-column prop="create_time" label="" align='center' width="160px"></el-table-column>
      </el-table>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
    </div>
    <div class='table' v-if='personType==1'>
      <div class='title'>
        <span class='title-text'></span>
        <el-input v-model="searchObj.search" class='query' placeholder=""></el-input>
        <el-button @click="getPersonInfoList" icon="el-icon-search" circle></el-button>
        <el-button class='pos-btn' type="primary" @click='cancel'></el-button>
      </div>
      <div class='text'>
        <span class='price'>:{{sumbitForm.total_price}}</span>
        <span class='price'>:{{sumbitForm.count}}</span>
      </div>
      <el-table :data="tableDataS" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column prop="batch_name" label="" align='center'></el-table-column>
        <el-table-column prop="apply_name" label="" align='center'></el-table-column>
        <el-table-column prop="create_time" label="" align='center'></el-table-column>
        <el-table-column prop="broker_num" label="" align='center' width="80px"></el-table-column>
        <el-table-column prop="chargebacks_price" label="" align='center' width="80px"></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">{{scope.row.broker_num-scope.row.chargebacks_price}}</template>
        </el-table-column>
        <el-table-column prop="check_name" label="" align='center'></el-table-column>
        <el-table-column prop="check_time" label="" align='center'></el-table-column>
        <el-table-column prop="document_num" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="recive_bank" label="" align='center'></el-table-column>
        <el-table-column prop="recive_bank_card" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="recive_name" label="" align='center'></el-table-column>
        <el-table-column prop="update_time" label="" align='center'></el-table-column>
      </el-table>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
    </div>
    <div class='table' v-if='personType==2'>
      <div class='title'>
        <span class='title-text'></span>
        <span class='search-block'>
          <span>
            <el-input v-model="searchObj.search" class='query' placeholder="/"></el-input>
            <el-button @click="getPersonInfoList" icon="el-icon-search" circle class='mt-10'></el-button>
          </span>
          <el-button class='pos-btn' type="primary" @click='cancel'></el-button>
        </span>
      </div>
      <div class='text'>
        <span class='price'>:{{sumbitForm.total_price}}</span>
        <span class='price'>:{{sumbitForm.count}}</span>
      </div>
      <el-table :data="tableDataSa" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px">
          <template slot-scope="scope">{{getIndex(scope)}}</template>
        </el-table-column>
        <el-table-column prop="client_id" label="" align='center'></el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="house_info" label="" align='center'></el-table-column>
        <el-table-column prop="name" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="tel" label="" align='center'></el-table-column>
        <el-table-column prop="broker_type" label="" align='center'>
          <template slot-scope="scope">{{brokerType(scope.row.broker_type)}}</template>
        </el-table-column>
        <!-- <el-table-column prop="create_time" label="" align='center' width="160px"></el-table-column> -->
        <el-table-column prop="broker_num" label="" align='center'></el-table-column>
        <el-table-column prop="commission_way" label="" align='center'></el-table-column>
        <el-table-column prop="create_time" label="" align='center' width="160px"></el-table-column>
      </el-table>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      tableDataS: [],
      tableDataSa: [],
      project_id: "",
      company_rule_id: "",
      searchObj: {
        search: "",
        page: 1
      },
      pageSize: 0,
      total: 0,
      sumbitForm: {
        total_price: "",
        count: ""
      },
      personType: 0 //0   1    2 
    };
  },
  mounted() {
    this.company_id = this.$route.params.company_id;
    this.personType = this.$route.params.personType;
    this.project_id = this.$route.params.project_id;
    if (!this.project_id) {
      this.$router.push({ name: "commission" });
      return;
    }
    this.company_rule_id = this.$route.params.company_rule_id;
    this.getPersonInfoList();
  },
  methods: {
    pageChange(page) {
      this.searchObj.page = page;
      this.getPersonInfoList();
    },
    getIndex(row) {
      let index = row.$index + 1 + (this.searchObj.page - 1) * this.pageSize;
      return index;
    },
    search() {
      this.searchObj.page = 1;
      this.getPersonInfoList();
    },
    async getPersonInfoList() {
      if (this.personType == 0) {
        let res = await this.api.getPersonInfoList({
          project_id: this.project_id,
          search: this.searchObj.search
        });
        if (res.code == 200) {
          this.tableData = res.data.broker_info.data;
          this.sumbitForm.total_price = res.data.total_price;
          this.sumbitForm.count = res.data.count;
          this.total = res.data.broker_info.total;
          this.pageSize = res.data.broker_info.per_page;
        }
      } else if (this.personType == 2) {
        //   else if (this.personType == 1) {
        //     let res = await this.api.getCompanyYInfo({
        //       project_id: this.project_id,
        //       company_id: this.company_id,
        //       search: this.searchObj.search
        //     });
        //     if (res.code == 200) {
        //       this.tableDataS = res.data.batch_list.data;
        //       this.sumbitForm.total_price = res.data.total_price;
        //       this.sumbitForm.count = res.data.count;
        //     }
        //   }
        let res = await this.api.getPersonNInfo({
          project_id: this.project_id,
          search: this.searchObj.search
        });
        if (res.code == 200) {
          this.tableDataSa = res.data.broker_info.data;
          this.sumbitForm.total_price = res.data.total_price;
          this.sumbitForm.count = res.data.count;
          this.total = res.data.broker_info.total;
          this.pageSize = res.data.broker_info.per_page;
        }
      }
    },
    cancel() {
      this.$router.push({
        name: "commission",
        params: this.$route.params
      });
    },
    brokerType(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },
    state(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    }
  }
};
</script>



